<template>
  <nv-card>
    <div class="flex flex-wrap-reverse items-center mb-base">
      <vs-button class="p-3 mr-4" @click="cancel" color="primary" type="filled" icon="fa-chevron-left" icon-pack="far">{{ $t('BACK') }}</vs-button>
      <vs-button class="p-3 mr-4" @click="modify" color="primary" type="filled" icon="fa-pen" icon-pack="far" v-if="$acl.check('isLimitAll')">{{ $t('MODIFY') }}</vs-button>
    </div>
    <div class="nv-row hover">
      <div class="nv-col sm:w-1/2 w-full">
        <p class="info-row">
          <span>{{ $t('ID') }}:</span>
          <span>{{ limit.limitId }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('TYPE') }}:</span>
          <span>{{ limit.limitTypeDescription }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('APP') }}:</span>
          <span>{{ limit.appName }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('TRANSFER_TYPE') }}:</span>
          <span>{{ limit.transferTypeName }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('FREQUENCY') }}:</span>
          <span>{{ limit.limitFrequencyDescription }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('CURRENCY_TYPE') }}:</span>
          <span>{{ limit.currencyTypeDescription }}</span>
        </p>
        <p class="info-row" v-if="limit.currencyTypeEnumId === 'FT_CCY_EXPLICIT'">
          <span>{{ $t('TRANSFER_CURRENCY') }}:</span>
          <span>{{ limit.transferCurrencyAbbreviation | placeholder('-') }}</span>
        </p>
        <p class="info-row" v-if="limit.currencyTypeEnumId === 'FT_CCY_EXPLICIT'">
          <span>{{ $t('DEBIT_CURRENCY') }}:</span>
          <span>{{ limit.debitCurrencyAbbreviation | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('CREATION_DATE') }}:</span>
          <span>{{ limit.creationDate | formatUnixTimestamp }}</span>
        </p>
      </div>
      <div class="nv-col sm:w-1/2 w-full">
        <p class="info-row" v-if="limit.description">
          <span>{{ $t('DESCRIPTION') }}:</span>
          <span>{{ limit.description | placeholder('-')  }}</span>
        </p>
        <p class="info-row" v-if="limit.limitTypeEnumId === 'LMT_TYPE_USER'">
          <span>{{ $t('USER') }}:</span>
          <span>{{ limit.userFullName | placeholder('-') }}</span>
        </p>
        <p class="info-row" v-if="limit.limitTypeEnumId === 'LMT_TYPE_USER_GRP'">
          <span>{{ $t('USER_GROUP') }}:</span>
          <span>{{ limit.userGroupDescription | placeholder('-') }}</span>
        </p>
        <p class="info-row" v-if="limit.limitTypeEnumId === 'LMT_TYPE_ORG'">
          <span>{{ $t('PARTY') }}:</span>
          <span>{{ limit.organizationName | placeholder('-') }}</span>
        </p>

        <p class="info-row" v-if="limit.limitTypeEnumId === 'LMT_TYPE_ACCT'">
          <span>{{ $t('ACCOUNT') }}:</span>
          <span>{{ limit.accountId | placeholder('-') }}</span>
        </p>
        <p class="info-row" v-if="limit.limitTypeEnumId === 'LMT_TYPE_BEN'">
          <span>{{ $t('BENEFICIARY') }}:</span>
          <span>{{ limit.beneficiaryId | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('AMOUNT') }}:</span>
          <span>{{ limit.minAmount | currency('', 0) | placeholder('*') }} - {{ limit.maxAmount | currency('', 0) | placeholder('*') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('FROM_DATE') }}:</span>
          <span>{{ limit.fromDate | formatUnixDate }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('TO_DATE') }}:</span>
          <span>{{ limit.toDate | formatUnixDate }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('DISABLED') }}:</span>
          <span>{{ limit.disabled| prettyBool  }}</span>
        </p>
      </div>
    </div>
  </nv-card>
</template>

<script>
import LimitService from '@/services/limit/LimitService'
import DisabledClassMixin from '@/mixins/commons/DisabledClassMixin'

export default {
  name: 'limit-view',
  mixins: [DisabledClassMixin],
  data: function () {
    return {
      limit: {},
      popupActive: false
    }
  },
  methods: {
    async load () {
      let self = this
      LimitService.fundsTransferLimits({
        limitId: this.$route.params.id
      }).then(function (response) {
        self.limit = response.data.fundsTransferLimitList[0] || {}
      })
    },
    cancel () {
      this.$router.back()
    },
    modify () {
      this.$router.push({ name: 'ModifyLimit', params: { id: this.$route.params.id } })
    }
  },
  mounted () {
    this.load()
  }
}
</script>
